@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700&display=swap);
:root {
    --primaria: #0b090a;
    --secundaria: #6930c3;
    --tercearia: #f5f3f4;
    --quaternaria: #dab6fc;

    --amareloTom1: #ffff3f;
    --amareloTom2: #eeef20;
    --verdeTom1: #80b918;
    --verdeTom2: #bfd200;
    
    --completeTaskButtonFontColor: #81b91880;
    --saveButtonFontColor: #f5f3f475;

}
/* Global CSS*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}

.App {
    height: 100vh;
    background-color: var(--primaria);
}

button {
    cursor: pointer;
}

#wrapper {
    border: solid 5px var(--secundaria);
    border-radius: 7px;
    box-shadow: 0px 0px 20px var(--secundaria);
    height: 80vh;
    width: 90%;
    margin:  auto;
    display: grid;
    overflow-y: hidden;
    grid-template-columns: 30% 70%;
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
}


#listWrapper {
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
}

#list {
    flex-grow: 1;
    list-style-type: none;
    border-right: solid 2px var(--secundaria);
    box-shadow: inset 10px 0px 150px #6830c327;

}

.listItem {
    margin: 7px 10px;
    border: solid 2px var(--quaternaria);
    border-radius: 3px;
    box-shadow: 0px 0px 10px var(--quaternaria);
    display: grid;
    grid-template-columns: 80% 20%;
    background: rgba(0,0,0,0);
    min-height: 3rem;
    place-items: center start;
}

.selected {
    background: rgba(255,255,255,0.1);
}

.listItem p {
    width: 100%;
    padding: 10px 0 10px 15px;
    font-size: 20px;
    color: #faf9f9d2;
}

.listItem button {
    place-self: center;
    display: flex;
    width: 30px;
    background: none;
    border: none;
}

.newTitleInput {
    display: flex;
    padding: 0 0 0 15px;
    height: 100%;
    width: 120%;
    background: none;
    color: var(--tercearia);
    font-size: 20px;
    border: none;
    outline: none;
}

.listItem:hover > #editOrRemoveDiv {
    visibility: visible;
}

.listItem button svg {
    height: 15px;
    pointer-events: none;
}

.listItem button:hover svg path {
    stroke: white;
}

#editOrRemoveDiv {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-content: center;
    visibility: hidden;
}

#newListItem {
    margin: 7px 10px;
    border: solid 2px var(--quaternaria);
    border-radius: 3px;
    box-shadow: 0px 0px 10px var(--quaternaria);
    background: rgba(0,0,0,0);
    height: 3rem;
    display: flex;
    padding: 0px 0px 0px 15px;
}

#newTaskInput {
    padding: 10px 0;
    width: 100%;
    background: rgba(0,0,0,0);
    color: rgba(255,255,255,0.7);
    font-size: 20px;
    border: none;
    outline: none;
}

.completed {
    border: solid 2px var(--verdeTom2);
    background: #c1d2002c;
}

.completed p {
    color: #ffff3f85;
}



#taskSection {
    display: flex;
    flex-direction: column;
}

textarea {
    width: 100%;
    height: 100%;
    padding: 10px;
    background: none;
    color: var(--quaternaria);
    resize: none;
    border: none;
    outline: none;
    font-size: 20px;
}

#taskSettingsOnDisplay {
    padding: 15px 5px;
    display: grid;
    grid-auto-flow: column;
    place-items: center;
    width: 100%;
    box-shadow: inset 0px 12px 50px #6830c327;
    border-top: solid 2px var(--secundaria);
}

#taskTimeData {
    flex-grow: 1;
    padding: 10px 4px;
    color: white;
}

#taskTimeData p {
    margin: 10px 4px;
}

#taskTimeData p span {
    color: var(--quaternaria);
}

#saveOrComplete {
    display: flex;
}

#saveOrComplete button {
    flex-grow: 1;
    padding: 10px;
    margin: 6px;
    font-weight: 500;
    background: rgba(0,0,0,0);
    font-size: large;
    transition: 300ms color ease, 300ms box-shadow ease, 300ms border-color ease;
    border-radius: 7px;
}

#saveButton {
    border: solid 3px var(--saveButtonFontColor);
    color: var(--saveButtonFontColor);
}

#completeTaskButton {
    border: solid 3px var(--completeTaskButtonFontColor);
    color: var(--completeTaskButtonFontColor);
}

#saveButton:hover {
    border: solid 3px var(--tercearia);
    box-shadow: 0px 0px 10px var(--tercearia);
    color: var(--tercearia);
    background: #f5f3f40c;
}

#completeTaskButton:hover {
    border: solid 3px var(--verdeTom1);
    box-shadow: 0px 0px 10px var(--verdeTom1);
    color: var(--verdeTom1);
    background: #81b9182a;
}

